import React, {useState} from 'react'
import {DisplayContainer, VideoBg, DisplayBg, DisplayContent, DisplayH1, DisplayP, DisplayBtnWrapper, ArrowForward, ArrowRight} from './DisplayElements'
import Video from '../../videos/display.mp4'
import { Button } from '../ButtonElements'
import TextLoop from "react-text-loop"

const DisplaySection = () => {

    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }

    return (
        <DisplayContainer id="display">
            <DisplayBg>
            <VideoBg playsInline autoPlay loop muted src={Video} type='video/mp4' />     Your browser does not support the video tag.
            </DisplayBg>
            <DisplayContent>
                <DisplayH1>
                    Hi, Welcome To GuildApp
                </DisplayH1>
                <TextLoop interval={2000} springConfig={{ stiffness: 340, damping: 20 }} mask={true}>
                    <DisplayP>🎉  Quest IRL</DisplayP>
                    <DisplayP>🎉  Start Your Adventure </DisplayP>
                    <DisplayP>🎉  Get Early Access Today</DisplayP>
                </TextLoop>{" "}
                <DisplayBtnWrapper>
                    <Button onMouseEnter={onHover} onMouseLeave={onHover} primary='true' dark="true" onClick={(e) => (window.location = 'https://theguildapp.typeform.com/to/usl9WuRt')}>
                    Get Early Access{hover ? <ArrowForward /> : <ArrowRight/>} 
                    </Button>
                </DisplayBtnWrapper>
            </DisplayContent>
        </DisplayContainer>
    )
}

export default DisplaySection
